.options {
  margin-bottom: 1rem;
}
.result{
  display: flex;
  width: 100%;
  padding: .5rem;
}
.resultImage{
  display: flex;
  flex: 0.4;
  /* height: 50rem; */
  background: #000;
}
.resultImage img{
  width: 100%;
  object-fit: cover;
}
.resultText{
  display: flex;
  flex: 0.6;
  flex-direction: column;
  padding: 2rem;
}
.resultText img{
  max-height: 5rem;
  object-fit: contain;
}
.resultText h1{
  font-size: 3rem;
}
.timerContainer{
  display: flex;
  justify-content: flex-end;
}
.testTimer{
  display: flex;
  padding: 1rem 3rem;
  border: 1px solid #00a19c;
  border-radius: 10px;
  font-size: 2rem;
  color:#00a19c;
}