.navigation {
  display: flex;
  height: calc(100vh - 4rem);
  flex: 0.15;
  flex-direction: column;
  padding: 0 1rem;
  border-radius: 0px;
  /* background: #43b8b1; */
  /* justify-content: space-between; */
}

.logo {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  max-height: 5rem;
  padding: 0.5rem;
  background: white;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  border-radius: 5px;
}

.logo img {
  width: 20%;
  margin-right: 0.5rem;
}

.links {
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

.links ul {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 100%;
  flex-direction: column;
}

.links ul li {
  display: flex;
  list-style: none;
  justify-content: flex-start;
  height: 3rem;
  cursor: pointer;
  padding: 0 1rem;
  align-items: center;
  margin-bottom: 0.5rem;
}

.links ul li a {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-decoration: none;
  align-items: center;
  text-decoration: none;
  color: gray;
}
.links ul li span {
  display: flex;
  list-style: none;
  justify-content: space-between;
  text-decoration: none;
  align-items: center;
  text-decoration: none;
  color: gray;
}
.links ul li a svg {
  margin-right: 0.5rem;
  font-size: 1.3rem;
}
.links ul li span svg {
  margin-right: 0.5rem;
  font-size: 1.3rem;
}

.links ul .active {
  background: #e9f0f0;
  border-radius: 5px;
}
.links ul .active a svg {
  color: #43b8b1;
}
.links ul .active a {
  color: black;
  font-weight: bold;
}
